import React, { useState } from 'react';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';

import Close from '@material-ui/icons/Close';

import Paper from '@material-ui/core/Paper';
import cloneDeep from 'lodash/fp/cloneDeep';
import find from 'lodash/fp/find';
import findIndex from 'lodash/fp/findIndex';
import map from 'lodash/fp/map';
import sortBy from 'lodash/fp/sortBy';

import { hexMe, textColor, toProperCase } from '../shared/utils';

export default function ColorSelector(props) {
    const { colors, name, onCancel, selectedColors: initialColors , updateSelectedColors } = props;

    const [selectedColors, setSelectedColors] = useState(initialColors);


    const saveColors = () => {
        if (typeof (Storage) !== "undefined") {
            const cols = map(c => `~${c[0]}`, selectedColors);
            localStorage.setItem(name, cols.join(''));
        }
        updateSelectedColors(selectedColors);
    }

    const handleCheck = e => {
        const selectedId = parseInt(e.target.value, 10);
        const workingColors = cloneDeep(selectedColors);
        const existing = findIndex(col => selectedId === col[0], selectedColors);

        if (existing !== -1) {
            // clear it from selectedColors
            workingColors.splice(existing, 1);
        } else {
            // get full color entry from colors
            const color = find(selectedColor => selectedId === selectedColor[0], colors);
            workingColors.push(color);
            sortBy(c => c[0], workingColors);
        }

        setSelectedColors(sortBy(c => c[0], workingColors));
    }

    const toggleAllCheckboxes = () => {
        if (selectedColors.length < colors.length) {
            // select all
            setSelectedColors(colors);
        } else {
            //deselect all
            setSelectedColors([]);
        }
    }

    return (
        <Dialog onClose={onCancel} open >
            <DialogTitle>
                Select Colors
                <IconButton aria-label="close" onClick={onCancel} style={{ position: 'absolute', right: 8, top: 8 }}>
                    <Close />
                </IconButton>
                <div>
                    <input type="checkbox" onClick={toggleAllCheckboxes} checked={selectedColors.length === colors.length} /> Select All
                    <Button style={{ marginLeft: "15px" }} size="small" variant="contained" color="primary" onClick={saveColors}>Save Selected Colors</Button>
                    <Button size="small" variant="contained" color="secondary" onClick={onCancel}>Cancel</Button>
                </div>
                <hr />
            </DialogTitle>
            <DialogContent dividers={scroll === 'paper'}>
                <div className="color-list">
                {map(c => {
                    const hex = `#${hexMe(c[2])}${hexMe(c[3])}${hexMe(c[4])}`;
                    const txt = textColor(c[2], c[3], c[4]);
                    const checked = findIndex(col => c[0] === col[0], selectedColors) !== -1;

                    return (
                        <div key={c[0]} className="colorBox">
                            <input type="checkbox" value={c[0]} checked={checked} onChange={handleCheck} />
                            <span className="colorRow" style={{ backgroundColor: hex, color: txt }}>
                                {`${toProperCase(c[1])} (#${c[0]})`}
                            </span>
                        </div>
                    );
                }, colors)}
                </div>
            </DialogContent>
        </Dialog>
    );
}




