const workercode = () => {
    const rgbToHsl = (r, g, b) => {
        r /= 255;
        g /= 255;
        b /= 255;
        const max = Math.max(r, g, b);
        const min = Math.min(r, g, b);
        let h;
        let s;
        let l = (max + min) / 2;

        if (max === min) {
            h = 0;
            s = 0; // achromatic
        } else {
            var d = max - min;
            s = l > 0.5 ? d / (2 - max - min) : d / (max + min);
            switch (max) {
                case r: h = (g - b) / d + (g < b ? 6 : 0); break;
                case g: h = (b - r) / d + 2; break;
                case b: h = (r - g) / d + 4; break;
                default: break;
            }
            h /= 6;
        }

        return [h, s, l];
    };

    const processThreads = (data, colors) => {
        // Note: Leave this vanilla JS for Web workers
        var diff;
        var rgbDiff;
        var hslDiff;

        for (var index = 0; index < data.length; index += 4) {
            if (data[index + 3] === 0) {
                //transparent pixel
                data[index] = 255;
                data[index + 1] = 255;
                data[index + 2] = 255;
                data[index + 3] = 0;
            } else {
                var r = data[index];
                var g = data[index + 1];
                var b = data[index + 2];

                var hsl = rgbToHsl(r, g, b);
                var hu = hsl[0];
                var s = hsl[1];
                var l = hsl[2];

                var curDiff = -1;
                for (var i = 0; i < colors.length; i++) {
                    var r2 = colors[i][2];
                    var g2 = colors[i][3];
                    var b2 = colors[i][4];
                    var hsl2 = rgbToHsl(r2, g2, b2);
                    var h2 = hsl2[0];
                    var s2 = hsl2[1];
                    var l2 = hsl2[2];

                    hslDiff = ((hu - h2) ** 2) + ((s - s2) ** 2) + ((l - l2) ** 2);
                    rgbDiff = ((r - r2) ** 2) + ((g - g2) ** 2) + ((b - b2) ** 2);
                    diff = hslDiff + rgbDiff;

                    if (curDiff === -1 || diff < curDiff) {
                        data[index] = r2;
                        data[index + 1] = g2;
                        data[index + 2] = b2;
                        curDiff = diff;
                    }
                }
            }
        }
    };

    // eslint-disable-next-line
    self.addEventListener('message', e => {
        //local copy of colors, to be passed and merged
        var localColors = e.data.colors;
        var threaddata = e.data.data;
        processThreads(threaddata, localColors);
        // eslint-disable-next-line
        self.postMessage({ result: threaddata, index: e.data.index, localColors: localColors });
    });
};

let code = workercode.toString();
code = code.substring(code.indexOf("{") + 1, code.lastIndexOf("}"));

const blob = new Blob([code], { type: "application/javascript" });
const workerScript = URL.createObjectURL(blob);
export default workerScript;
