import React, { useState } from 'react';

import AppBar from '@material-ui/core/AppBar';
import Button from "@material-ui/core/Button";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

import logo from '../App/dave.svg';
import '../../css/app.css';
import theme from '../../css/theme';
import SupplyLinks from '../shared/SupplyLinks';
import GlassEditor from "../GlassEditor";
import PatternEditor from '../PatternEditor';
import PBNEditor from "../PBNEditor";

const siteTheme = createMuiTheme(theme);

export default function App() {
    const [create, setCreate] = useState("cross-stitch");
    const [showEditor, setShowEditor] = useState(false);
    const [patternData, setPatternData] = useState(null);
    const [patternMetaData, setPatternMetadata] = useState(null);

    const handleFileSelect = evt => {
        const { files } = evt.target;

        // eslint-disable-next-line
        for (var i = 0, f; f = files[i]; i++) {
            if (!f.type.match('image.*')) continue;

            var reader = new FileReader();
            reader.onload = (function (theFile) {
                evt.target.value = '';
                return function (e) {
                    setShowEditor(true);
                    setPatternData(e.target.result);
                    setPatternMetadata(null); 
                };
            })(f);
            reader.readAsDataURL(f);
        }
    }

    return (
        <MuiThemeProvider theme={siteTheme}>
            <AppBar position="sticky">
                <Toolbar style={{ display: "flex" }}>
                    <img src={logo} className="logo" alt="logo" />
                    <div style={{ color: '#F8F8F8', flexGrow: 1 }}>
                        <div style={{ fontWeight: "bold" }}>Dave's Craft Designer</div>
                        <div>Create Craft Patterns from Images -- FREE</div>
                    </div>
                    <a href="/" style={{ color: "#FFF", fontSize: ".9rem", textDecoration: "underline" }}>Back to codingDave.com</a>
                </Toolbar>
            </AppBar>
            <AppBar position="sticky" color="secondary">
                <div style={{ display: "flex" }}>
                    <div id="leftCol" style={{ flexGrow: 1 }}>
                        <Typography variant="h6">1) Upload an Image:</Typography>
                        <div className="upload-btn-wrapper">
                            <Button variant="contained" color="primary" type="button" className="btn">Select Image</Button>
                            <input
                                type="file"
                                id="files"
                                name="files"
                                onChange={handleFileSelect}
                            />
                        </div>
                        <Typography variant="h6">2) What would you like to create?</Typography>
                        <FormControl component="span" style={{ padding: "4px" }}>
                            <RadioGroup
                                aria-label="What would you like to create?"
                                name="create"
                                value={create}
                                onChange={e => setCreate(e.target.value)}
                                style={{ display: "block" }}
                            >
                                <FormControlLabel value="cross-stitch" control={<Radio color="primary" />} label="Cross-Stitch Pattern" />
                                <FormControlLabel value="pbn" control={<Radio color="primary" />} label="Painting" />
                                <FormControlLabel value="glass" control={<Radio color="primary" />} label="Fused Glass" />
                            </RadioGroup>
                        </FormControl>
                    </div>
                    <div id="rightCol">
                        <SupplyLinks />
                    </div>
                </div>
            </AppBar>
            <div className="App">
                <div>
                    <div style={{ display: 'flex' }}>
                        <div className="content" style={{ padding: '12px' }}>
                            <div>
                                {showEditor && create ? (
                                    create == "cross-stitch" ? <PatternEditor data={patternData} metaData={patternMetaData} /> :
                                    create == "pbn" ? <PBNEditor data={patternData} metaData={patternMetaData} /> :
                                    <GlassEditor data={patternData} metaData={patternMetaData} />
                                ) : null }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{ margin: '20px' }}>
                <Typography component="span" variant="caption">
                    Send any questions or comments to hikingdave @ gmail.com | &copy; 2018-{new Date().getFullYear()}
                </Typography>
                <Typography component="span" variant="caption">
                    No images or data are sent to the server. Everything you see exists solely in your browser.  
                </Typography>
            </div>
        </MuiThemeProvider>
    );
}
