import React from 'react';
import { hexMe } from './utils';


export default function ColorBox(props) {
    const { color } = props;
    const style = {
        width: '10px',
        height: '10px',
        backgroundColor: `#${hexMe(color[2])}${hexMe(color[3])}${hexMe(color[4])}`,
        border: '1px solid #999',
    }

    return <span title={`${color[0]}: ${color[1]}`} style={style}>&nbsp;</span>;
}
