import { hexMe, getColor } from "./utils";

export default function getImagesFromData(data, cellsize, colors) {
    const imgData = data.id ?
        new ImageData(Uint8ClampedArray.from(data.imagedata), data.width, data.height) : data;

    // draw preview image
    const workingCanvas = document.createElement('canvas');
    workingCanvas.width = data.width;
    workingCanvas.height = data.height;
    const context = workingCanvas.getContext("2d");
    context.putImageData(imgData, 0, 0);

    const previewImage = workingCanvas.toDataURL("image/png");

    // draw pattern image
    let cellrow = 0;
    let cellcolumn = 0;

    const midW = Math.floor(imgData.width / 2) - 1;
    const midH = Math.floor(imgData.height / 2);
    let offsetW = 0;
    let offsetH = 0;

    const estHours = Math.floor(imgData.data.length / 4 / 200);
    workingCanvas.height = imgData.height * cellsize + Math.floor(imgData.height / 10) + 2;
    workingCanvas.width = imgData.width * cellsize + Math.floor(imgData.width / 10) + 2;
    const imgcontext = workingCanvas.getContext("2d");
    const threadList = {};

    //first set entire canvas to white, so we can just ignore transparent pixels
    imgcontext.fillStyle = "#FFF";
    imgcontext.fillRect(0, 0, workingCanvas.width, workingCanvas.height);

    for (let y = 0; y < imgData.height; y++) {
        for (let x = 0; x < imgData.width; x++) {
            let index = (y * imgData.width * 4) + (x * 4);
            let r = imgData.data[index];
            let g = imgData.data[index + 1];
            let b = imgData.data[index + 2];
            let curColor = getColor(r, g, b, colors);

            if (curColor) {
                if (imgData.data[index + 3] > 0) { //skip transparent pixels
                    imgcontext.fillStyle = "#000000";
                    imgcontext.fillRect(cellcolumn * cellsize + offsetW, cellrow * cellsize + offsetH, cellsize, cellsize);
                    imgcontext.fillStyle = ["rgb(", r, ",", g, ",", b, ")"].join('');
                    imgcontext.fillRect(cellcolumn * cellsize + 1 + offsetW, cellrow * cellsize + 1 + offsetH, cellsize, cellsize);
                    imgcontext.fillStyle = ["rgb(", 256 - r, ",", 256 - g, ",", 256 - b, ")"].join('');
                    imgcontext.font = '10px sans-serif';
                    imgcontext.fillText(curColor[0].toString(), cellcolumn * cellsize + 2 + offsetW, (cellrow + 1) * cellsize - 5 + offsetH);
                }

                if (threadList[curColor[0]]) {
                    threadList[curColor[0]].count += 1;
                } else {
                    threadList[curColor[0]] = {
                        count: 1,
                        number: curColor[0],
                        name: curColor[1],
                        color: `#${hexMe(curColor[2])}${hexMe(curColor[3])}${hexMe(curColor[4])}`
                    };
                }
            }

            //grid lines
            if (cellcolumn === midW) {
                //if cell is the middle, add 3 black lines to the right
                imgcontext.fillStyle = "#000000";
                imgcontext.fillRect(cellcolumn * cellsize + offsetW + cellsize + 1, cellrow * cellsize + offsetH, 3, cellsize + 1);
                offsetW += 3;
            } else if (cellcolumn % 10 === midW % 10) {
                //if cell is 10 off from middle (or at middle), add 1 black line to the right
                imgcontext.fillStyle = "#000000";
                imgcontext.fillRect(cellcolumn * cellsize + offsetW + cellsize, cellrow * cellsize + offsetH, 1, cellsize + 1);
                offsetW += 1;
            }

            cellcolumn += 1;
        }

        //grid lines
        if (cellrow === midH) {
            //if row is the middle, add 2 black lines below entire row
            imgcontext.fillStyle = "#000000";
            imgcontext.fillRect(0, cellrow * cellsize + offsetH + 1, imgData.width * cellsize, 2);
            offsetH += 1;
        } else if (cellrow % 10 === midH % 10) {
            //if row is 10 off from middle, or at middle, add 1 black line below entire row
            imgcontext.fillStyle = "#000000";
            imgcontext.fillRect(0, cellrow * cellsize + offsetH + 1, imgData.width * cellsize, 1);
            offsetH += 1;
        }


        offsetW = 0;
        cellrow += 1;
        cellcolumn = 0;
    }

    const patternImage = workingCanvas.toDataURL("image/png");

    return {
        imgData: imgData,
        previewImage: previewImage,
        patternImage: patternImage,
        estHours: estHours,
        threadList: threadList,
    };
}
