import React from 'react';

import PBNColors from '../colors/PBNColors';
import NonPixelEditor from "../shared/NonPixelEditor";
import { detectEdges, flattenColors, oilPaintEffect } from '../shared/utils';


export default function PBNEditor(props) {
    const { data } = props;

    const postProcess = (patterncontext, imgData, colors, width, height) => {
        flattenColors(patterncontext, imgData, colors);
        oilPaintEffect(patterncontext, width, height, 7, 185); 
        detectEdges(patterncontext, width, height); 
    }

    return <NonPixelEditor
        allColors={PBNColors}
        data={data}
        postProcess={postProcess}
        storageName="pbncolors"
    />
}


