import React from 'react';

import GlassColors from '../colors/GlassColors';
import NonPixelEditor from "../shared/NonPixelEditor";
import { flattenColors, oilPaintEffect } from '../shared/utils';

export default function GlassEditor(props) {
    const { data } = props;

    const postProcess = (patterncontext, imgData, colors, width, height) => {
        flattenColors(patterncontext, imgData, colors);
        oilPaintEffect(patterncontext, width, height, 4, 125); 
    }

    return <NonPixelEditor
        allColors={GlassColors}
        data={data}
        postProcess={postProcess}
        storageName="glasscolors"
    />

}
