import React, { useEffect, useState } from 'react';

import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import Drawer from "@material-ui/core/Drawer";
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Hidden from "@material-ui/core/Hidden";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from '@material-ui/core/ListItemText';
import Switch from '@material-ui/core/Switch';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import ReactImageMagnify from 'react-image-magnify';
import saveAs from 'file-saver';

import map from 'lodash/fp/map';

import DMCColors from '../colors/DMCColors';

import ColorBox from './ColorBox';
import ColorSelector from './ColorSelector';
import getImagesFromData from './getImagesFromData';
import workerScript from './processing';
import { processThreads } from './utils';


export default function PixelEditor(props) {
    const { allColors, data, postProcess, storageName } = props;
    const imageRef = React.createRef();
    const workingCanvasRef = React.createRef();
    const patternCanvasRef = React.createRef();

    const [colors, setColors] = useState(allColors);
    const [activeStep, setActiveStep] = useState(1);
    const [imgWidth, setImgWidth] = useState(0);
    const [imgHeight, setImgHeight] = useState(0);
    const [maxProjectWidth, setMaxProjectWidth] = useState(12);
    const [showStitchCount, setShowStitchCount] = useState(false);
    const [showColorSelector, setShowColorSelector] = useState(false);
    const [pattern, setPattern] = useState(null);
    const [working, setWorking] = useState(false);
    const [downloading, setDownloading] = useState(false);
    const [isPhoto, setIsPhoto] = useState(true);
    const [stitchPerInch, setStitchPerInch] = useState(14);

    useEffect(() => {
        // on load, read selected colors from localStorage, if present
        if (typeof (Storage) !== "undefined") {
            if (localStorage[storageName]) {
                const myColors = [];
                const localColors = localStorage[storageName].split("~");
                localColors.forEach(c => {
                    const cInt = parseInt(c, 10);
                    const fullColor = colors.find(col => cInt === col[0]);
                    if (fullColor) {
                        myColors.push(fullColor);
                    }
                });
                setColors(myColors);
            }
        }
    }, []);

    useEffect(() => {
        if (downloading) {
            const img = document.createElement('img');
            img.onload = () => {
                const canvas = document.createElement('canvas');
                canvas.setAttribute('width', img.width);
                canvas.setAttribute('height', img.height);

                const ctx = canvas.getContext('2d');
                ctx.drawImage(img, 0, 0);
                canvas.toBlob((blob) => {
                    saveAs(blob, "Design.png");
                    setDownloading(false);
                });
            };
    
            img.setAttribute('src', pattern.patternImage);
        }
    }, [downloading]);

    useEffect(() => {
        // reset if we get a new image
        setPattern(null);
        setActiveStep(1);
    }, [data]);

    useEffect(() => {
        if (!working) return;
        // if we were told to start working, build or update the pattern

        const workingCanvas = workingCanvasRef.current;
        const context = workingCanvas.getContext("2d");

        // size it.
        let ratio = 1;
        const image = imageRef.current;
        workingCanvas.width = image.width;
        workingCanvas.height = image.height;
        const max = parseInt(maxProjectWidth, 10) * parseInt(stitchPerInch, 10);
        

        //rescale if necessary
        if (isPhoto && workingCanvas.width > max) {
            ratio = max / image.width;
            workingCanvas.width = image.width * ratio;
            workingCanvas.height = image.height * ratio;
            context.scale(ratio, ratio);
        }

        context.drawImage(imageRef.current, 0, 0, image.width, image.height);

        // create/update the pattern
        let imgData = context.getImageData(0, 0, context.canvas.width, context.canvas.height); //get scaled image from canvas
        const patternCanvas = patternCanvasRef.current; // expanded pixels -> cells
        const previewContext = patternCanvas.getContext('2d');
        patternCanvas.width = imgData.width;
        patternCanvas.height = imgData.height;
        previewContext.drawImage(workingCanvas, 0, 0);

        imgData = previewContext.getImageData(0, 0, imgData.width, imgData.height); // 1:1 pixel data

        if (!window.Worker) {
            console.log('no workers');
            //single threaded processing - do it, set it, be done.
            processThreads(imgData.data, colors);
            previewContext.putImageData(imgData, 0, 0);

            setImgWidth(imgData.width)
            setImgHeight(imgData.height);
            setPattern(getImagesFromData(imgData, 22, colors)); 
            setActiveStep(2);
            setWorking(false);
            return;
        }

        // web workers for parallel processing
        let workersCount = (navigator.hardwareConcurrency) ? navigator.hardwareConcurrency : 2;
        let blockSize = imgData.data.length / workersCount;
        let finished = 0;

        // reduce workers until each one has a blockSize that does not split pixels
        while (blockSize % 4 !== 0) {
            workersCount--;
            blockSize = imgData.data.length / workersCount;
        }

        for (let index = 0; index < workersCount; index++) {
            const worker = new Worker(workerScript);

            // eslint-disable-next-line
            worker.addEventListener('message', e => {
                const threadData = e.data.result;
                const { index } = e.data;

                //put the thread data back into imgData
                if (threadData.length > 0) {
                    imgData.data.set(threadData, blockSize * index);
                }

                finished++;
                if (finished === workersCount) {
                    // last worker is done - set all the things.
                    setImgWidth(imgData.width)
                    setImgHeight(imgData.height);
                    setPattern(getImagesFromData(imgData, 22, colors)); 
                    setActiveStep(2);
                    setWorking(false);
                }
            });

            const message = { data: imgData.data.slice(blockSize * index, (blockSize * index) + blockSize), index: index, colors: colors };
            worker.postMessage(message);
        }
    }, [working]);

    const updateSelectedColors = newColors => {
        setColors(newColors);
        setShowColorSelector(false);
    }

    const todayDate = () => {
        var today = new Date();
        var dd = today.getDate();
        var mm = today.getMonth()+1;
        var yyyy = today.getFullYear();
    
        if(dd<10) { dd='0'+dd }
        if(mm<10) { mm='0'+mm }
    
        return mm+'/'+dd+'/'+yyyy;
    
    }

    const downloadSupplyList = () => {
        if (!pattern) return;

        var doc = new jsPDF();
        doc.setFont("helvetica");
        doc.setFontSize(12);
        doc.text(8, 8, "Design created " + todayDate() + " at https://codingdave.com/designer");
        doc.text(8, 16, "Colors used in this design:");
        let row = 3
        const keys = Object.keys(pattern.threadList);
        keys.forEach((key, index) => {
            row +=1;
            doc.text(8, row * 8, "#" + pattern.threadList[key].number + ": " + pattern.threadList[key].name + " - " + pattern.threadList[key].count);
            if (row * 8 > 270) {
                doc.addPage();
                row = 1;
            }
        });

        doc.save('SupplyList.pdf');
    
    }

    const supplyDisplay = pattern ? map(c => (
        <ListItem key={c.number}>
            <ListItemIcon>
                <svg width="16" height="16" version="1.1" xmlns="http://www.w3.org/2000/svg">
                    <rect x="0" y="0" width="16" height="16" fill={c.color} />
                </svg>
            </ListItemIcon>
            <ListItemText primary={`#${c.number} - ${c.name}`} secondary={c.count} />
        </ListItem>
    ), pattern.threadList) : null;

    return (
        <div className="pattern-editor">
            {activeStep === 1 ? (
                <>
                    <Drawer
                        id="options-panel"
                        variant="persistent"
                        anchor="left"
                        open
                    >
                        <Typography variant="h6">Options:</Typography>
                        <FormControl fullWidth>
                            <FormControlLabel
                                control={(
                                    <Switch
                                        checked={isPhoto}
                                        onChange={e => setIsPhoto(e.target.checked)}
                                        value={isPhoto}
                                        color="primary"
                                    />
                                )}
                                label={(
                                    <span>
                                        Is this a photo?
                                        <FormHelperText margin="dense" component="div">
                                            Photos are re-sized to the project size.
                                            Non-photos (pixel art, sprites, etc) are not, and are designed at one stitch per pixel.
                                        </FormHelperText>
                                    </span>
                                )}
                            />
                        </FormControl>
                        <TextField
                            id="maxProjectWidth"
                            name="maxProjectWidth"
                            value={maxProjectWidth}
                            type="number"
                            onChange={e => setMaxProjectWidth(e.target.value)}
                            label="Max Project Width (in inches)"
                            margin="dense"
                            variant="outlined"
                            helperText="Wider projects have more detail, but also take more time, and may slow your browser as it calculates."
                        />
                        <TextField
                            id="stitchPerInch"
                            name="stitchPerInch"
                            value={stitchPerInch}
                            type="number"
                            onChange={e => setStitchPerInch(e.target.value)}
                            label="Stitches per inch"
                            margin="dense"
                            variant="outlined"
                        />
                        {showColorSelector ? (
                            <FormControl>
                                <ColorSelector
                                    name={storageName}
                                    colors={DMCColors}
                                    selectedColors={colors}
                                    onCancel={() => setShowColorSelector(false)}
                                    updateSelectedColors={updateSelectedColors}
                                />
                            </FormControl>
                        ) : (
                            <div>
                                <FormControl>
                                    <FormControlLabel
                                        control={(
                                            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                                                {colors ? colors.map(c => <ColorBox key={c[0]} color={c} />) : null}
                                            </div>
                                        )}
                                        label={`Current Colors (${colors.length}):`}
                                        labelPlacement="top"
                                    />
                                </FormControl>
                                <Button variant="contained" size="small" color="secondary" className="color-button" onClick={() => setShowColorSelector(true)}>Adjust Colors</Button>
                            </div>
                        )}
                    </Drawer>
                    <div className="upload-preview">
                        <Button
                            variant="contained"
                            color="primary"
                            className="create-button"
                            onClick={() => setWorking(true)}
                        >
                            {working ? "Working..." : pattern ? "Update Design" : "Create Design"}
                        </Button>
                        <div style={{ display: 'block', margin: '0 auto' }}>
                            <img alt="source" src={data} style={{ display: 'block', margin: '15px', maxWidth: '400px' }} />
                        </div>
                    </div>
                </>
            ) : null
            }

            {pattern && activeStep === 2 && (
                <div className="row">
                    <div style={{ marginTop: '15px' }} className="pattern-container">
                        <Button variant="contained" size="small" color="primary" onClick={() => setDownloading(true)}>{downloading ? "Preparing Download..." : "Download Design (PNG)"}</Button>
                        <Button size="small" variant="contained" color="secondary" onClick={() => setActiveStep(1)}>Change Options</Button>
                        <Hidden mdUp>
                                <Button size="small" variant="contained" color="secondary" onClick={() => setShowStitchCount(true)}>Show Color List</Button>
                        </Hidden>
                        <Divider />
                        <Hidden mdUp>
                            <Drawer
                                variant="temporary"
                                anchor="right"
                                open={showStitchCount}
                                onClose={() => setShowStitchCount(false)}
                                ModalProps={{
                                    keepMounted: true,
                                }}
                            >
                                <Typography variant="h6">Color List</Typography>
                                <Button size="small" variant="contained" color="secondary" onClick={downloadSupplyList}>Download List (PDF)</Button>
                                <List dense>
                                    {supplyDisplay}
                                </List>
                            </Drawer>
                        </Hidden>
                        <Hidden smDown>
                            <Drawer
                                variant="permanent"
                                anchor="right"
                                open
                            >
                                <Typography variant="h6">Color List</Typography>
                                <Button size="small" variant="contained" color="secondary" onClick={downloadSupplyList}>Download List (PDF)</Button>
                                <List dense>
                                    {supplyDisplay}
                                </List>
                            </Drawer>
                        </Hidden>
                        <Typography variant="caption">(Mouseover to display details)</Typography>
                        <ReactImageMagnify
                            style={{ zIndex: '49999' }}
                            {...{
                                smallImage: {
                                    alt: 'Pattern Preview',
                                    src: pattern.previewImage,
                                    width: pattern.imgData.width,
                                    height: pattern.imgData.height,
                                },
                                largeImage: {
                                    src: pattern.patternImage,
                                    width: pattern.imgData.width * 22,
                                    height: pattern.imgData.height * 22,
                                },
                                enlargedImageContainerDimensions: {
                                    width: 400,
                                    height: 400,
                                }
                            }} />
                    </div>
                </div>
            )}

            {/* hidden canvasses for processing */}
            <img
                alt="Uploaded Image"
                ref={imageRef}
                src={data}
                style={{ position: 'relative', left: -10000, maxWidth: `${maxProjectWidth*stitchPerInch}px` }}
            />
            <canvas ref={patternCanvasRef} style={{ position: 'relative', left: -10000, display: 'none' }} />
            <canvas style={{ display: 'none' }} ref={workingCanvasRef} />
        </div>
    );
}


