import { hexMe, getColor } from "./utils";

export default function getSupplyListFromData(data, colors) {
    const imgData = data.id ?
        new ImageData(Uint8ClampedArray.from(data.imagedata), data.width, data.height) : data;

    const threadList = {};

    for (let y = 0; y < imgData.height; y++) {
        for (let x = 0; x < imgData.width; x++) {
            let index = (y * imgData.width * 4) + (x * 4);
            let r = imgData.data[index];
            let g = imgData.data[index + 1];
            let b = imgData.data[index + 2];
            let curColor = getColor(r, g, b, colors);

            if (curColor) {
                if (threadList[curColor[0]]) {
                    threadList[curColor[0]].count += 1;
                } else {
                    threadList[curColor[0]] = {
                        count: 1,
                        number: curColor[0],
                        name: curColor[1],
                        color: `#${hexMe(curColor[2])}${hexMe(curColor[3])}${hexMe(curColor[4])}`
                    };
                }
            }
        }
    }

    return threadList;
}
